/*
reset css
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  /* font: inherit; */
  vertical-align: baseline;
}
/*
  Colors
*/
/*
  Typos
*/
/*
  variables
*/
html {
  font-size: 1em;
}
body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.45;
  color: #424242;
}
p {
  margin-bottom: 1.3em;
}
h1, h2, h3, h4 {
  margin: 1.414em 0 0.5em;
  font-weight: 700;
  line-height: 1.2;
}
h1 {
  margin-top: 0;
  font-size: 3.998em;
}
h2 {
  font-size: 2.827em;
}
h3 {
  font-size: 1.999em;
}
h4 {
  font-size: 1.414em;
}
small, .font_small {
  font-size: 0.707em;
}
/*
  General
*/
body {
  background: #F7F7F7;
}
.container {
  max-width: 1024px;
  margin: 0 auto;
}
a {
  color: #4A90E2;
  text-decoration: none;
}
a:hover {
    text-decoration: underline;
}
/*
  button
*/
a.button {
  padding: .8em 1em;
  border-radius: 5px;
}
a.button.default {
  background: #424242;
  color: white;
  -webkit-transition: .3s;
  transition: .3s;
}
a.button.default:hover {
    text-decoration: none;
    background: #5c5c5c;
}
a.button.dropbox {
  background: #0460FF;
  color: white;
  -webkit-transition: .3s;
  transition: .3s;
}
a.button.dropbox:hover {
    text-decoration: none;
    background: #3780ff;
}
