.navigation {
  background: white;
  padding: 1.5em 2em;
  font-size: 1.3em;
  border-bottom: 1px solid #DBDBDB;
}
.navigation .link-title {
    text-decoration: none;
    color: #424242;
}
.navigation .link-title:hover {
      text-decoration: underline;
}
.navigation .separator {
    margin: 0 .5em;
}
.navigation .title {
    font-weight: 700;
}
.folders {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 2em;
}
.folder-wrapper {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 1em 1em;
  margin: 0.5em;
  width: calc(50% - 1em);
  background: white;
  border: 1px solid #DBDBDB;
  border-radius: 5px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.folder-wrapper a {
    font-size: 1.3em;
    color: #424242;
    text-decoration: none;
}
.folder-wrapper .folder::before {
    content: url(data:image/svg+xml;base64,);
    display: inline-block;
    vertical-align: middle;
    margin-right: .5em;
}
.folder-wrapper .folder.specs::before {
    content: url(data:image/svg+xml;base64,) !important;
}

.specs-wrapper {
position: fixed;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
width: 100%;
height: 100vh;
}
.iframe {
    height: 100%;
    width: 100%;
    background: #191919;
}

.sidebar {
  width: 500px;
  height: 100%;
  background: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.sidebar .content {
    padding: 1em;
    overflow: scroll;
}

.sidebar-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  background: white;
  padding: 1em 1em;
}
.sidebar-header a {
    display: block;
}
.sidebar-header a::before {
      content: url(data:image/svg+xml;base64,);
      display: inline-block;
      vertical-align: 10%;
      margin-right: .5em;
}
.sidebar-header span {
    font-weight: 700;
    font-size: 1.5em;
}
